.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.blank {
  color: rgb(218, 86, 86);
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: 0.2s;
}

.blank:hover {
  color: rgb(11, 204, 101);
  cursor: pointer;
  text-decoration: underline;

}

.inputClass {
  margin: 10px;
  background-color: white;
  border-radius: 5px;
  border: solid;
  border-color: gray;
}

.welcome {
  margin: 10px;
}